.intro-wrapper {
    font-size: 13px; 
    margin: 0 auto;
    padding-left: 10%;
    padding-right: 10%;
    /* font-family: 'Jeju Gothic', serif; */
    }
    
.intro-div-wrapper {
    padding: 10px;
    line-height: 1.3em;
}

.intro-div-wrapper ul {
    list-style: inside;
    color: rgb(85, 150, 255);
    
}

.intro-div-wrapper ul li a {
    font-size: 0.9rem;
    line-height: 1.3em;
    cursor: pointer;
}

.intro-div-wrapper ul li a:hover {
    font-weight: bold;
}


.intro-div-wrapper h5 {
    line-height: 2em;
    font-weight: bold;
}

.intro-div-wrapper table,
.intro-div-wrapper th,
.intro-div-wrapper td{
    font-size: 0.9em;
    border: 1px solid black;
    padding: 0.5em;
}